import { put, takeLatest } from 'redux-saga/effects';
import { subjectActionTypes as at, GetSubjectListType, getSubjectListSuccess } from '../models/subject';

export function* watchGetSubjectList() {
	yield takeLatest(at.GET_SUBJECT_LIST, getSubjectList);
}

function* getSubjectList(action: GetSubjectListType) {
	// ownerId question[][] 
	const res = [
		[
			{
				id: 0,
				subjectId: 0,
				order: 0,
				text: '.    .  .',
				selectedIndexes: [ 0, 3, 5, 6 ]
			},
			{
				id: 1,
				subjectId: 0,
				order: 1,
				text: '.    .  .',
				selectedIndexes: [ 2 ]
			}
		],
		[
			{
				id: 2,
				subjectId: 1,
				order: 0,
				text: '.    .  .',
				selectedIndexes: [ 0, 3, 5, 6 ]
			},
			{
				id: 3,
				subjectId: 1,
				order: 1,
				text: '.    .  .',
				selectedIndexes: [ 0, 3, 5, 6 ]
			},
			{
				id: 4,
				subjectId: 1,
				order: 2,
				text: '.    .  .',
				selectedIndexes: [ 0, 3, 5, 6 ]
			}
		]
	];

	yield put(getSubjectListSuccess(res));
}
